<template>
    <div class='person'>
      <img  :src='src' class='head' width="50%" height="100%">
      <div class="message">
       <div class='info' >{{name}} </div>

        <div class="messageLength" @click="infoDisplay(),readMessage()"></div>
      </div>
    </div>
</template>
<style scoped>
    .person{
      float : right;
      height: 100%;
      width:10%;
    }
    .head{
      float:left;
      border-radius : 50%;
    }
    .message{
      float:left;
      width:50%;
      height:100%;
    }
    .info{
      width:100%;
      height:40%;
      margin-top:10%;

    }
    .messageLength{
      margin-left:10%;
      width:100%;
      height:50%;
      background-image:url(//localhost:3000/images/email.png) ;
      background-repeat:no-repeat;
      background-size:50% 60%;
      background-position: center center;
      color:red;
      font-size:20px;
    }


</style>
<script>
    import store from '../../store'
    import {getUserInfo, handlePicPath, createSocket} from '../../tools'
    export default{
        data(){
              let userInfo = getUserInfo();
              return{
                src : handlePicPath(userInfo.headPicture),
                name : userInfo.userName,
                arrow : 'data:image/png;base64,',
              }

        },
        methods :{
           infoDisplay(){
                store.dispatch('changeDisplay');
           },
          readMessage(){
               this.messageList = [];
               store.dispatch('readMessage');
          }
        },
        computed: {
          messageLength　(){
              return store.state.message.data.length;
          }
        },
        components:{

        }
    }
</script>



